/* src/styles/Experience.css */
.experience-container {
  padding: 40px 20px;
  max-width: 1000px;
  margin: 0 auto;
  color: #f1f1f1;
}

.section-title {
  font-size: 32px;
  margin-bottom: 32px;
  text-align: center;
  color: #00ffff;
}

.timeline {
  position: relative;
  padding-left: 20px;
  border-left: 2px solid #00ffff55;
}

.timeline-item {
  margin-bottom: 40px;
  position: relative;
}

.timeline-dot {
  width: 14px;
  height: 14px;
  background-color: #00ffff;
  border-radius: 50%;
  position: absolute;
  left: -8px;
  top: 4px;
}

.timeline-content {
  background-color: #1f1f1f;
  padding: 20px;
  border-radius: 12px;
  border-left: 4px solid #00ffff40;
  box-shadow: 0 0 10px rgba(0, 255, 255, 0.05);
}

.timeline-content h3 {
  font-size: 20px;
  margin-bottom: 6px;
  color: #00ffff;
}

.duration {
  font-size: 14px;
  color: #aaaaaa;
  margin-bottom: 12px;
  display: block;
}

.timeline-content p {
  font-size: 15px;
  margin-bottom: 10px;
  color: #dddddd;
}

.timeline-content ul {
  padding-left: 18px;
  color: #cccccc;
}

.timeline-content ul li {
  margin-bottom: 6px;
  font-size: 14px;
}
