.sidebar {
  width: 280px;
  height: 100vh;
  background: white;
  color: var(--text-primary);
  padding: 2.5rem 1.8rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow-y: auto;
  border-right: 1px solid rgba(0,0,0,0.05);
}

/* Profile Image */
.profile-pic {
  width: 140px;
  height: 140px;
  object-fit: cover;
  border-radius: 50%;
  border: 3px solid transparent;
  background: var(--gradient-primary) border-box;
  margin-bottom: 2rem;
  transition: all 0.3s;
}

.profile-pic:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px rgba(110, 142, 251, 0.3);
}

/* Text & Typography */
h1 {
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 24px;
  text-align: left;
  line-height: 1.4;
  color: var(--text-primary);
}

.title-container {
  margin-bottom: 1.5rem; /* Added consistent margin */
  gap: 10px; /* Increased from 8px */
}

.role-pill {
  background: var(--gradient-primary);
  color: white;
  border-radius: 20px;
  padding: 4px 12px;
  font-size: 0.7rem;
  font-weight: 500;
}

.subtitle {
  font-size: 0.8rem;
  color: var(--text-secondary);
  text-align: center;
  margin-bottom: 32px;
}

/* Contact Info */
.contact-info {
  margin: 1.2rem 0; /* Balanced top/bottom */
}

.contact-item,
.contact-link {
  color: var(--text-secondary);
  transition: all 0.3s;
  margin-bottom: 1rem; /* Increased from 0.8rem */
  padding: 6px 0; /* Added vertical breathing room */
}

.contact-link:hover {
  color: #6e8efb;
}

/* Social Icons */
.social-icon {
  color: var(--text-secondary);
  transition: all 0.3s;
  font-size: 1.6rem; /* Slightly larger */
}

.social-icon:hover {
  color: #6e8efb;
  transform: scale(1.2);
}

.social-footer {
  margin-top: 2rem; /* Increased from 1rem */
  gap: 24px; /* Increased from 20px */
}